import React, { useState, useEffect, useRef, useContext } from 'react';
import {
  Box,
  Container,
  Typography,
  TextField,
  Button,
  Paper,
  Grid,
  Avatar,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
  Divider,
  IconButton,
  CircularProgress,
  Badge,
  useTheme,
  useMediaQuery
} from '@mui/material';
import {
  Send as SendIcon,
  MoreVert as MoreVertIcon,
  Search as SearchIcon,
  FavoriteBorder as FavoriteBorderIcon
} from '@mui/icons-material';
import { GeminiContext } from '../context/GeminiContext';
import { AuthContext } from '../context/AuthContext';

// 
const girls = [
  {
    id: 1,
    name: '',
    age: 25,
    profession: '',
    avatar: 'https://randomuser.me/api/portraits/women/44.jpg',
    lastMessage: '',
    lastMessageTime: '14:30',
    unread: 1,
    personality: '',
    interests: ['', '', ''],
    bio: ''
  },
  {
    id: 2,
    name: '',
    age: 23,
    profession: '',
    avatar: 'https://randomuser.me/api/portraits/women/67.jpg',
    lastMessage: '',
    lastMessageTime: '',
    unread: 0,
    personality: '',
    interests: ['', '', ''],
    bio: ''
  },
  {
    id: 3,
    name: '',
    age: 27,
    profession: '',
    avatar: 'https://randomuser.me/api/portraits/women/33.jpg',
    lastMessage: '',
    lastMessageTime: '',
    unread: 2,
    personality: '',
    interests: ['', '', ''],
    bio: ''
  },
  {
    id: 4,
    name: '',
    age: 24,
    profession: '',
    avatar: 'https://randomuser.me/api/portraits/women/74.jpg',
    lastMessage: '(;`)',
    lastMessageTime: '3',
    unread: 0,
    personality: '',
    interests: ['', '', ''],
    bio: ''
  },
  {
    id: 5,
    name: '',
    age: 26,
    profession: '',
    avatar: 'https://randomuser.me/api/portraits/women/17.jpg',
    lastMessage: '',
    lastMessageTime: '1',
    unread: 0,
    personality: '',
    interests: ['', '', ''],
    bio: 'ITAI'
  }
];

// 
interface Message {
  id: string;
  content: string;
  sender: 'user' | 'ai';
  timestamp: Date;
}

// 
interface Conversation {
  id: number;
  messages: Message[];
}

const MessagesPage: React.FC = () => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));
  const { user } = useContext(AuthContext);
  const { sendMessage, isLoading } = useContext(GeminiContext);
  
  // 
  const [selectedGirl, setSelectedGirl] = useState(girls[0]);
  const [input, setInput] = useState('');
  const [searchTerm, setSearchTerm] = useState('');
  const [showChatList, setShowChatList] = useState(!isMobile);
  const [conversations, setConversations] = useState<Record<number, Message[]>>({});
  const messagesEndRef = useRef<HTMLDivElement>(null);

  // 
  const filteredGirls = girls.filter(girl => 
    girl.name.toLowerCase().includes(searchTerm.toLowerCase())
  );

  // 
  useEffect(() => {
    // 
    const storedConversations = localStorage.getItem('conversations');
    if (storedConversations) {
      try {
        setConversations(JSON.parse(storedConversations));
      } catch (e) {
        console.error('Failed to parse stored conversations:', e);
        initializeConversations();
      }
    } else {
      initializeConversations();
    }
  }, []);

  // 
  const initializeConversations = () => {
    const initialConversations: Record<number, Message[]> = {};
    
    girls.forEach(girl => {
      initialConversations[girl.id] = [
        {
          id: `${girl.id}-1`,
          content: `${user?.name || ''}${girl.name}${girl.age}${girl.profession}${girl.bio}`,
          sender: 'ai',
          timestamp: new Date(Date.now() - 1000 * 60 * 5) // 5
        }
      ];
    });
    
    setConversations(initialConversations);
    localStorage.setItem('conversations', JSON.stringify(initialConversations));
  };

  // 
  const getCurrentConversation = () => {
    return conversations[selectedGirl.id] || [];
  };

  // 
  const handleSendMessage = async () => {
    if (!input.trim()) return;
    
    // 
    const userMessage: Message = {
      id: `user-${Date.now()}`,
      content: input,
      sender: 'user',
      timestamp: new Date()
    };
    
    // 
    const currentConversation = [...getCurrentConversation(), userMessage];
    const updatedConversations = {
      ...conversations,
      [selectedGirl.id]: currentConversation
    };
    
    setConversations(updatedConversations);
    localStorage.setItem('conversations', JSON.stringify(updatedConversations));
    setInput('');
    
    // Gemini API
    try {
      const prompt = `${selectedGirl.name}${selectedGirl.age}${selectedGirl.profession}${selectedGirl.personality}${selectedGirl.interests.join('')}${user?.name || ''}${user?.name || ''}: "${input}"

:
1. ${selectedGirl.name}
2. 400
3. 
4. 
5. 
6. `;

      const aiResponse = await sendMessage(prompt);
      
      // AI
      const aiMessage: Message = {
        id: `ai-${Date.now()}`,
        content: aiResponse || '',
        sender: 'ai',
        timestamp: new Date()
      };
      
      const updatedWithAiResponse = [
        ...updatedConversations[selectedGirl.id],
        aiMessage
      ];
      
      const finalConversations = {
        ...updatedConversations,
        [selectedGirl.id]: updatedWithAiResponse
      };
      
      setConversations(finalConversations);
      localStorage.setItem('conversations', JSON.stringify(finalConversations));
    } catch (error) {
      console.error('Failed to get AI response:', error);
    }
  };

  // 
  const handleSelectGirl = (girl: typeof girls[0]) => {
    setSelectedGirl(girl);
    if (isMobile) {
      setShowChatList(false);
    }
  };

  // 
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [conversations]);

  // 
  const toggleChatList = () => {
    setShowChatList(!showChatList);
  };

  return (
    <Container 
      maxWidth="lg" 
      sx={{ 
        height: 'calc(100vh - 120px)', 
        display: 'flex', 
        flexDirection: 'column',
        py: 2
      }}
    >
      <Typography 
        variant="h5" 
        component="h1" 
        sx={{ 
          mb: 2, 
          fontWeight: 600,
          display: { xs: 'flex', md: 'block' },
          alignItems: 'center',
          justifyContent: 'space-between'
        }}
      >
        
        {isMobile && (
          <Button 
            variant="outlined"
            onClick={toggleChatList}
            size="small"
          >
            {showChatList ? '' : ''}
          </Button>
        )}
      </Typography>
      
      <Grid 
        container 
        spacing={2} 
        sx={{ 
          flexGrow: 1,
          height: 'calc(100vh - 180px)'
        }}
      >
        {/*  */}
        {(showChatList || !isMobile) && (
          <Grid 
            item 
            xs={12} 
            md={4} 
            sx={{ 
              height: '100%',
              display: 'flex',
              flexDirection: 'column'
            }}
          >
            <Paper 
              sx={{ 
                p: 2, 
                height: '100%',
                display: 'flex',
                flexDirection: 'column'
              }}
            >
              <Box sx={{ mb: 2 }}>
                <TextField
                  fullWidth
                  placeholder="..."
                  variant="outlined"
                  size="small"
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  InputProps={{
                    startAdornment: <SearchIcon color="action" sx={{ mr: 1 }} />,
                  }}
                />
              </Box>
              
              <List sx={{ flexGrow: 1, overflow: 'auto' }}>
                {filteredGirls.map((girl) => (
                  <React.Fragment key={girl.id}>
                    <ListItem 
                      button 
                      alignItems="flex-start"
                      selected={selectedGirl.id === girl.id}
                      onClick={() => handleSelectGirl(girl)}
                      sx={{
                        borderRadius: 1,
                        mb: 1,
                        '&.Mui-selected': {
                          backgroundColor: theme.palette.primary.light + '20',
                          '&:hover': {
                            backgroundColor: theme.palette.primary.light + '30',
                          },
                        },
                      }}
                    >
                      <ListItemAvatar>
                        <Badge
                          color="error"
                          badgeContent={girl.unread}
                          invisible={girl.unread === 0}
                        >
                          <Avatar 
                            alt={girl.name} 
                            src={girl.avatar}
                            sx={{ width: 50, height: 50 }}
                          />
                        </Badge>
                      </ListItemAvatar>
                      <ListItemText
                        primary={
                          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                            <Typography variant="subtitle1" component="span" sx={{ fontWeight: 600 }}>
                              {girl.name}
                            </Typography>
                            <Typography variant="caption" color="text.secondary">
                              {girl.lastMessageTime}
                            </Typography>
                          </Box>
                        }
                        secondary={
                          <React.Fragment>
                            <Typography
                              variant="body2"
                              component="span"
                              sx={{
                                display: 'inline',
                                color: girl.unread > 0 ? 'text.primary' : 'text.secondary',
                                fontWeight: girl.unread > 0 ? 500 : 400,
                              }}
                            >
                              {girl.lastMessage}
                            </Typography>
                          </React.Fragment>
                        }
                      />
                    </ListItem>
                    <Divider variant="inset" component="li" />
                  </React.Fragment>
                ))}
              </List>
            </Paper>
          </Grid>
        )}
        
        {/*  */}
        {(!showChatList || !isMobile) && (
          <Grid 
            item 
            xs={12} 
            md={8} 
            sx={{ 
              height: '100%',
              display: 'flex',
              flexDirection: 'column'
            }}
          >
            <Paper 
              sx={{ 
                p: 0, 
                height: '100%',
                display: 'flex',
                flexDirection: 'column',
                overflow: 'hidden'
              }}
            >
              {/*  */}
              <Box 
                sx={{ 
                  p: 2, 
                  borderBottom: `1px solid ${theme.palette.divider}`,
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'space-between'
                }}
              >
                <Box sx={{ display: 'flex', alignItems: 'center' }}>
                  {isMobile && (
                    <IconButton 
                      edge="start" 
                      color="inherit" 
                      onClick={toggleChatList}
                      sx={{ mr: 1 }}
                    >
                      <SearchIcon />
                    </IconButton>
                  )}
                  <Avatar 
                    alt={selectedGirl.name} 
                    src={selectedGirl.avatar}
                    sx={{ width: 40, height: 40, mr: 1.5 }}
                  />
                  <Box>
                    <Typography variant="subtitle1" sx={{ fontWeight: 600 }}>
                      {selectedGirl.name}
                    </Typography>
                    <Typography variant="caption" color="text.secondary">
                      {selectedGirl.age}{selectedGirl.profession}
                    </Typography>
                  </Box>
                </Box>
                <Box>
                  <IconButton color="primary">
                    <FavoriteBorderIcon />
                  </IconButton>
                  <IconButton>
                    <MoreVertIcon />
                  </IconButton>
                </Box>
              </Box>
              
              {/*  */}
              <Box 
                sx={{ 
                  p: 2, 
                  flexGrow: 1,
                  overflow: 'auto',
                  backgroundColor: theme.palette.background.default + '80',
                  backgroundImage: 'linear-gradient(rgba(255, 255, 255, 0.05) 1px, transparent 1px), linear-gradient(90deg, rgba(255, 255, 255, 0.05) 1px, transparent 1px)',
                  backgroundSize: '20px 20px'
                }}
              >
                {getCurrentConversation().map((message, index) => (
                  <Box
                    key={message.id}
                    sx={{
                      display: 'flex',
                      justifyContent: message.sender === 'user' ? 'flex-end' : 'flex-start',
                      mb: 2,
                    }}
                  >
                    {message.sender === 'ai' && (
                      <Avatar 
                        alt={selectedGirl.name} 
                        src={selectedGirl.avatar}
                        sx={{ 
                          width: 36, 
                          height: 36, 
                          mr: 1,
                          alignSelf: 'flex-end'
                        }}
                      />
                    )}
                    <Box
                      sx={{
                        maxWidth: '75%',
                        p: 2,
                        borderRadius: 2,
                        backgroundColor: message.sender === 'user' 
                          ? theme.palette.primary.main 
                          : theme.palette.background.paper,
                        color: message.sender === 'user' ? 'white' : 'inherit',
                        boxShadow: theme.shadows[1],
                      }}
                    >
                      <Typography variant="body1">
                        {message.content}
                      </Typography>
                      <Typography 
                        variant="caption" 
                        sx={{ 
                          display: 'block', 
                          mt: 1,
                          textAlign: message.sender === 'user' ? 'right' : 'left',
                          opacity: 0.7
                        }}
                      >
                        {new Date(message.timestamp).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
                      </Typography>
                    </Box>
                    {message.sender === 'user' && (
                      <Avatar 
                        alt={user?.name || 'User'} 
                        src={user?.profileImage}
                        sx={{ 
                          width: 36, 
                          height: 36, 
                          ml: 1,
                          alignSelf: 'flex-end'
                        }}
                      />
                    )}
                  </Box>
                ))}
                <div ref={messagesEndRef} />
                
                {/*  */}
                {isLoading && (
                  <Box sx={{ display: 'flex', justifyContent: 'center', my: 2 }}>
                    <CircularProgress size={30} />
                  </Box>
                )}
              </Box>
              
              {/*  */}
              <Box 
                sx={{ 
                  p: 2, 
                  borderTop: `1px solid ${theme.palette.divider}`,
                  backgroundColor: theme.palette.background.paper
                }}
              >
                <Grid container spacing={1}>
                  <Grid item xs>
                    <TextField
                      fullWidth
                      placeholder="..."
                      variant="outlined"
                      value={input}
                      onChange={(e) => setInput(e.target.value)}
                      onKeyPress={(e) => e.key === 'Enter' && !e.shiftKey && handleSendMessage()}
                      disabled={isLoading}
                      multiline
                      maxRows={3}
                    />
                  </Grid>
                  <Grid item>
                    <Button
                      variant="contained"
                      color="primary"
                      endIcon={<SendIcon />}
                      onClick={handleSendMessage}
                      disabled={!input.trim() || isLoading}
                      sx={{ 
                        height: '100%',
                        px: 3
                      }}
                    >
                      
                    </Button>
                  </Grid>
                </Grid>
              </Box>
            </Paper>
          </Grid>
        )}
      </Grid>
    </Container>
  );
};

export default MessagesPage; 