import React, { useState, useEffect, ChangeEvent } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import {
  Box,
  Container,
  Typography,
  Paper,
  Button,
  Grid,
  Divider,
  Chip,
  Avatar,
  ListItemAvatar,
  ListItemText,
  TextField,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  CircularProgress,
  IconButton,
  Snackbar,
  Alert,
  Card,
  CardMedia,
  CardContent,
  useTheme,
  useMediaQuery
} from '@mui/material';
import {
  ArrowBack as ArrowBackIcon,
  CalendarMonth as CalendarIcon,
  Schedule as ScheduleIcon,
  LocationOn as LocationIcon,
  Group as GroupIcon,
  Favorite as FavoriteIcon,
  FavoriteBorder as FavoriteBorderIcon,
  Share as ShareIcon,
  AccessTime as AccessTimeIcon,
  PriceCheck as PriceIcon,
  Category as CategoryIcon
} from '@mui/icons-material';
import { motion } from 'framer-motion';
import { useAuth } from '../context/AuthContext';

// 
interface EventData {
  id: string;
  title: string;
  description: string;
  images: string[];
  date: string;
  time: string;
  location: string;
  participants: Participant[];
  maxParticipants: number;
  price: number;
  category: string;
  tags: string[];
  organizer: Organizer;
  isFavorite: boolean;
}

// 
interface Participant {
  id: string;
  name: string;
  avatar: string;
  age: number;
  bio: string;
}

// 
interface Organizer {
  id: string;
  name: string;
  avatar: string;
  rating: number;
  eventsHosted: number;
  bio: string;
}

// User
interface User {
  id: string;
  name: string;
  email: string;
  avatar?: string;
}

// 
const fadeIn = {
  hidden: { opacity: 0, y: 20 },
  visible: { opacity: 1, y: 0 }
};

const EventDetailPage = (): JSX.Element => {
  const { id } = useParams();
  const navigate = useNavigate();
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));
  const isSmall = useMediaQuery(theme.breakpoints.down('sm'));
  const { user } = useAuth();
  
  // 
  const [event, setEvent] = useState<EventData | null>(null);
  const [loading, setLoading] = useState(true);
  const [openDialog, setOpenDialog] = useState(false);
  const [dialogStep, setDialogStep] = useState(1);
  const [numParticipants, setNumParticipants] = useState(1);
  const [notes, setNotes] = useState('');
  const [submitting, setSubmitting] = useState(false);
  const [success, setSuccess] = useState(false);
  const [activeImage, setActiveImage] = useState(0);
  const [showAlert, setShowAlert] = useState(false);
  const [alertMessage, setAlertMessage] = useState('');
  const [alertSeverity, setAlertSeverity] = useState<'success' | 'error' | 'info'>('success');
  
  // 
  useEffect(() => {
    // API
    const fetchData = async () => {
      try {
        setLoading(true);
        
        // 
        const mockEvent: EventData = {
          id: '1',
          title: '',
          description: `\n\n\n\n\n\n\n\n\n\n\n`,
          images: [
            'https://images.unsplash.com/photo-1522383225653-ed111181a951?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1200&q=80',
            'https://images.unsplash.com/photo-1551088373-ecfa00934e1e?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1200&q=80',
            'https://images.unsplash.com/photo-1521127474489-d524412fd439?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1200&q=80'
          ],
          date: '202342',
          time: '12:00 - 17:00',
          location: ' ',
          participants: [
            { id: '1', name: '', avatar: 'https://randomuser.me/api/portraits/women/44.jpg', age: 28, bio: '' },
            { id: '2', name: '', avatar: 'https://randomuser.me/api/portraits/men/32.jpg', age: 35, bio: '' },
            { id: '3', name: '', avatar: 'https://randomuser.me/api/portraits/women/68.jpg', age: 25, bio: '' },
            { id: '4', name: '', avatar: 'https://randomuser.me/api/portraits/men/45.jpg', age: 30, bio: '' },
            { id: '5', name: '', avatar: 'https://randomuser.me/api/portraits/women/22.jpg', age: 27, bio: '' },
            { id: '6', name: '', avatar: 'https://randomuser.me/api/portraits/men/67.jpg', age: 33, bio: '' },
            { id: '7', name: '', avatar: 'https://randomuser.me/api/portraits/women/17.jpg', age: 29, bio: '' }
          ],
          maxParticipants: 20,
          price: 3000,
          category: '',
          tags: ['', '', '', '', ''],
          organizer: {
            id: '1',
            name: '',
            avatar: 'https://randomuser.me/api/portraits/men/1.jpg',
            rating: 4.5,
            eventsHosted: 24,
            bio: ''
          },
          isFavorite: false
        };
        
        // 1
        setTimeout(() => {
          setEvent(mockEvent);
          setLoading(false);
        }, 1000);
      } catch (error) {
        console.error('Error fetching event data:', error);
        setLoading(false);
      }
    };
    
    fetchData();
  }, [id]);
  
  // 
  const handleToggleFavorite = () => {
    if (event) {
      setEvent({
        ...event,
        isFavorite: !event.isFavorite
      });
      
      setAlertMessage(event.isFavorite ? '' : '');
      setAlertSeverity('success');
      setShowAlert(true);
    }
  };
  
  // 
  const handleShare = () => {
    // Web API Navigator
    if (navigator.clipboard && event) {
      navigator.clipboard.writeText(`${event.title}: https://lovematch.example.com/events/${event.id}`)
        .then(() => {
          setAlertMessage('');
          setAlertSeverity('success');
          setShowAlert(true);
        })
        .catch(() => {
          setAlertMessage('');
          setAlertSeverity('error');
          setShowAlert(true);
        });
    } else {
      setAlertMessage('');
      setAlertSeverity('error');
      setShowAlert(true);
    }
  };
  
  // 
  const handleOpenDialog = () => {
    setOpenDialog(true);
    setDialogStep(1);
    setNumParticipants(1);
    setNotes('');
  };
  
  // 
  const handleCloseDialog = () => {
    setOpenDialog(false);
  };
  
  // 
  const handleNextStep = () => {
    setDialogStep(2);
  };
  
  // 
  const handlePrevStep = () => {
    setDialogStep(1);
  };
  
  // 
  const handleParticipantChange = (e: ChangeEvent<HTMLInputElement>) => {
    const value = parseInt(e.target.value);
    setNumParticipants(value > 0 ? value : 1);
  };
  
  // 
  const handleNotesChange = (e: ChangeEvent<HTMLInputElement>) => {
    setNotes(e.target.value);
  };
  
  // 
  const handleSubmit = () => {
    setSubmitting(true);
    
    // API
    setTimeout(() => {
      setSubmitting(false);
      setSuccess(true);
      
      // 3
      setTimeout(() => {
        setOpenDialog(false);
        setSuccess(false);
        
        // 
        if (event && user) {
          const newParticipant: Participant = {
            id: Date.now().toString(),
            name: user.name || '',
            avatar: user.profileImage || 'https://randomuser.me/api/portraits/lego/1.jpg',
            age: 30,
            bio: ''
          };
          
          setEvent({
            ...event,
            participants: [...event.participants, newParticipant]
          });
          
          setAlertMessage('');
          setAlertSeverity('success');
          setShowAlert(true);
        }
      }, 3000);
    }, 2000);
  };
  
  // 
  const isAlreadyJoined = () => {
    if (!event || !user) return false;
    return event.participants.some((p: Participant) => p.id === (user as User).id);
  };
  
  // 
  const handleImageChange = (index: number) => {
    setActiveImage(index);
  };
  
  // 
  if (loading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100vh' }}>
        <CircularProgress />
      </Box>
    );
  }
  
  // 
  if (!event) {
    return (
      <Container maxWidth="md" sx={{ py: 5 }}>
        <Paper elevation={2} sx={{ p: 3, textAlign: 'center' }}>
          <Typography variant="h5" gutterBottom>
            
          </Typography>
          <Typography variant="body1" paragraph>
            
          </Typography>
          <Button variant="contained" color="primary" onClick={() => navigate('/events')}>
            
          </Button>
        </Paper>
      </Container>
    );
  }
  
  return (
    <Container maxWidth="lg" sx={{ py: { xs: 2, md: 4 } }}>
      <motion.div
        initial="hidden"
        animate="visible"
        variants={fadeIn}
        transition={{ duration: 0.5 }}
      >
        {/*  */}
        <Box sx={{ mb: 3, display: 'flex', alignItems: 'center' }}>
          <IconButton 
            color="inherit" 
            sx={{ mr: 1 }}
            onClick={() => navigate('/events')}
          >
            <ArrowBackIcon />
          </IconButton>
          <Typography variant="h4" component="h1" sx={{ fontWeight: 600 }}>
            
          </Typography>
        </Box>
        
        <Grid container spacing={3}>
          {/* :  */}
          <Grid item xs={12} md={7}>
            <Card sx={{ mb: 2, borderRadius: 2, overflow: 'hidden' }}>
              <CardMedia
                component="img"
                height={400}
                image={event.images[activeImage]}
                alt={event.title}
                sx={{ objectFit: 'cover' }}
              />
            </Card>
            
            {/*  */}
            <Grid container spacing={1} sx={{ mb: 3 }}>
              <Box sx={{ display: 'flex', gap: 1, mt: 2, flexWrap: 'wrap' }}>
                {event.images.map((image: string, index: number) => (
                  <Box
                    key={index}
                    onClick={() => handleImageChange(index)}
                    sx={{ 
                      cursor: 'pointer',
                      border: index === activeImage ? `2px solid ${theme.palette.primary.main}` : 'none',
                      borderRadius: 1,
                      height: 100,
                      overflow: 'hidden'
                    }}
                  >
                    <CardMedia
                      component="img"
                      height="100%"
                      image={image}
                      alt={`${event.title} -  ${index + 1}`}
                      sx={{ objectFit: 'cover' }}
                    />
                  </Box>
                ))}
              </Box>
            </Grid>
            
            {/*  */}
            {isMobile && (
              <Box sx={{ mb: 3 }}>
                <Typography variant="h5" component="h2" sx={{ fontWeight: 600, mb: 1 }}>
                  {event.title}
                </Typography>
                
                <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1, mb: 2 }}>
                  {event.tags.map((tag: string) => (
                    <Chip key={tag} label={tag} size="small" variant="outlined" />
                  ))}
                </Box>
                
                <Typography variant="body1" sx={{ whiteSpace: 'pre-line', mb: 2 }}>
                  {event.description}
                </Typography>
                
                <Box sx={{ display: 'flex', gap: 1 }}>
                  <Button
                    variant="outlined"
                    color={event.isFavorite ? 'secondary' : 'primary'}
                    startIcon={event.isFavorite ? <FavoriteIcon /> : <FavoriteBorderIcon />}
                    onClick={handleToggleFavorite}
                    sx={{ flex: 1 }}
                  >
                    {event.isFavorite ? '' : ''}
                  </Button>
                  <Button
                    variant="outlined"
                    color="primary"
                    startIcon={<ShareIcon />}
                    onClick={handleShare}
                    sx={{ flex: 1 }}
                  >
                    
                  </Button>
                </Box>
              </Box>
            )}
            
            {/*  */}
            <Paper elevation={1} sx={{ p: 3, borderRadius: 2, mb: 3 }}>
              <Typography variant="h6" sx={{ fontWeight: 600, mb: 2 }}>
                
              </Typography>
              
              <Grid container spacing={2}>
                <Grid item xs={12} sm={6}>
                  <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                    <CalendarIcon color="primary" sx={{ mr: 1 }} />
                    <Box>
                      <Typography variant="body2" color="text.secondary">
                        
                      </Typography>
                      <Typography variant="body1">
                        {event.date}
                      </Typography>
                    </Box>
                  </Box>
                </Grid>
                
                <Grid item xs={12} sm={6}>
                  <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                    <ScheduleIcon color="primary" sx={{ mr: 1 }} />
                    <Box>
                      <Typography variant="body2" color="text.secondary">
                        
                      </Typography>
                      <Typography variant="body1">
                        {event.time}
                      </Typography>
                    </Box>
                  </Box>
                </Grid>
                
                <Grid item xs={12}>
                  <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                    <LocationIcon color="primary" sx={{ mr: 1 }} />
                    <Box>
                      <Typography variant="body2" color="text.secondary">
                        
                      </Typography>
                      <Typography variant="body1">
                        {event.location}
                      </Typography>
                    </Box>
                  </Box>
                </Grid>
                
                <Grid item xs={12} sm={6}>
                  <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                    <GroupIcon color="primary" sx={{ mr: 1 }} />
                    <Box>
                      <Typography variant="body2" color="text.secondary">
                        
                      </Typography>
                      <Typography variant="body1">
                        {event.participants.length} / {event.maxParticipants}
                      </Typography>
                    </Box>
                  </Box>
                </Grid>
                
                <Grid item xs={12} sm={6}>
                  <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                    <PriceIcon color="primary" sx={{ mr: 1 }} />
                    <Box>
                      <Typography variant="body2" color="text.secondary">
                        
                      </Typography>
                      <Typography variant="body1">
                        {event.price.toLocaleString()}
                      </Typography>
                    </Box>
                  </Box>
                </Grid>
                
                <Grid item xs={12}>
                  <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                    <CategoryIcon color="primary" sx={{ mr: 1 }} />
                    <Box>
                      <Typography variant="body2" color="text.secondary">
                        
                      </Typography>
                      <Typography variant="body1">
                        {event.category}
                      </Typography>
                    </Box>
                  </Box>
                </Grid>
              </Grid>
            </Paper>
            
            {/*  */}
            <Paper elevation={1} sx={{ p: 3, borderRadius: 2 }}>
              <Typography variant="h6" sx={{ fontWeight: 600, mb: 2 }}>
                 ({event.participants.length})
              </Typography>
              
              <Grid container spacing={1} sx={{ mt: 1 }}>
                {event.participants.map((participant: Participant) => (
                  <Grid item xs={4} sm={3} md={2} key={participant.id}>
                    <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                      <Avatar
                        src={participant.avatar}
                        alt={participant.name}
                        sx={{ width: 50, height: 50, mb: 1 }}
                      />
                      <Typography variant="body2" sx={{ textAlign: 'center', fontSize: '0.8rem' }}>
                        {participant.name}
                      </Typography>
                    </Box>
                  </Grid>
                ))}
              </Grid>
            </Paper>
          </Grid>
          
          {/* :  */}
          <Grid item xs={12} md={5}>
            {/*  */}
            {!isMobile && (
              <Box sx={{ mb: 3 }}>
                <Typography variant="h5" component="h2" sx={{ fontWeight: 600, mb: 1 }}>
                  {event.title}
                </Typography>
                
                <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1, mb: 2 }}>
                  {event.tags.map((tag: string) => (
                    <Chip key={tag} label={tag} size="small" variant="outlined" />
                  ))}
                </Box>
                
                <Typography variant="body1" sx={{ whiteSpace: 'pre-line', mb: 2 }}>
                  {event.description}
                </Typography>
                
                <Box sx={{ display: 'flex', gap: 1 }}>
                  <Button
                    variant="outlined"
                    color={event.isFavorite ? 'secondary' : 'primary'}
                    startIcon={event.isFavorite ? <FavoriteIcon /> : <FavoriteBorderIcon />}
                    onClick={handleToggleFavorite}
                  >
                    {event.isFavorite ? '' : ''}
                  </Button>
                  <Button
                    variant="outlined"
                    color="primary"
                    startIcon={<ShareIcon />}
                    onClick={handleShare}
                  >
                    
                  </Button>
                </Box>
              </Box>
            )}
            
            {/*  */}
            <Paper elevation={1} sx={{ p: 3, borderRadius: 2, mb: 3 }}>
              <Typography variant="h6" sx={{ fontWeight: 600, mb: 2 }}>
                
              </Typography>
              
              <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                <Avatar
                  src={event.organizer.avatar}
                  alt={event.organizer.name}
                  sx={{ width: 60, height: 60, mr: 2 }}
                />
                <Box>
                  <Typography variant="subtitle1" sx={{ fontWeight: 500 }}>
                    {event.organizer.name}
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    : {event.organizer.eventsHosted}
                  </Typography>
                </Box>
              </Box>
              
              <Typography variant="body2" paragraph>
                {event.organizer.bio}
              </Typography>
            </Paper>
            
            {/*  */}
            <Paper 
              elevation={2} 
              sx={{ 
                p: 3, 
                borderRadius: 2,
                border: `1px solid ${theme.palette.primary.main}`,
              }}
            >
              <Typography variant="h6" sx={{ fontWeight: 600, mb: 1 }}>
                
              </Typography>
              
              <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                <AccessTimeIcon color="warning" sx={{ mr: 1, fontSize: 20 }} />
                <Typography variant="body2" color="text.secondary">
                   - {event.maxParticipants - event.participants.length}
                </Typography>
              </Box>
              
              <Divider sx={{ my: 2 }} />
              
              <Box sx={{ mb: 3 }}>
                <Typography variant="subtitle1" sx={{ fontWeight: 500, mb: 1 }}>
                  
                </Typography>
                <Typography variant="h5" color="primary" sx={{ fontWeight: 600 }}>
                  {event.price.toLocaleString()}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  * 
                </Typography>
              </Box>
              
              <Button
                variant="contained"
                color="primary"
                fullWidth
                size="large"
                onClick={handleOpenDialog}
                disabled={isAlreadyJoined()}
                sx={{ 
                  py: 1.5,
                  borderRadius: 2
                }}
              >
                {isAlreadyJoined() ? '' : ''}
              </Button>
              
              {isAlreadyJoined() && (
                <Typography variant="body2" color="text.secondary" sx={{ mt: 1, textAlign: 'center' }}>
                  
                </Typography>
              )}
            </Paper>
          </Grid>
        </Grid>
      </motion.div>
      
      {/*  */}
      <Dialog
        open={openDialog}
        onClose={handleCloseDialog}
        maxWidth="sm"
        fullWidth
      >
        <DialogTitle>
          {dialogStep === 1 ? '' : ''}
        </DialogTitle>
        
        <DialogContent>
          {dialogStep === 1 ? (
            // 1: 
            <>
              <DialogContentText sx={{ mb: 3 }}>
                
              </DialogContentText>
              
              <TextField
                label=""
                type="number"
                value={numParticipants}
                onChange={handleParticipantChange}
                fullWidth
                margin="normal"
                InputProps={{ inputProps: { min: 1, max: 5 } }}
                helperText="15"
              />
              
              <TextField
                label=""
                multiline
                rows={4}
                value={notes}
                onChange={handleNotesChange}
                fullWidth
                margin="normal"
                placeholder=""
              />
            </>
          ) : (
            // 2: 
            <>
              <DialogContentText sx={{ mb: 3 }}>
                
              </DialogContentText>
              
              <Box sx={{ mb: 2 }}>
                <Typography variant="subtitle2"></Typography>
                <Typography variant="body1" sx={{ mb: 1 }}>{event.title}</Typography>
                
                <Typography variant="subtitle2"></Typography>
                <Typography variant="body1" sx={{ mb: 1 }}>{event.date} {event.time}</Typography>
                
                <Typography variant="subtitle2"></Typography>
                <Typography variant="body1" sx={{ mb: 1 }}>{event.location}</Typography>
                
                <Typography variant="subtitle2"></Typography>
                <Typography variant="body1" sx={{ mb: 1 }}>{numParticipants}</Typography>
                
                <Typography variant="subtitle2"></Typography>
                <Typography variant="body1" sx={{ mb: 1, fontWeight: 600, color: theme.palette.primary.main }}>
                  {(event.price * numParticipants).toLocaleString()}
                </Typography>
                
                {notes && (
                  <>
                    <Typography variant="subtitle2"></Typography>
                    <Typography variant="body1" sx={{ mb: 1 }}>{notes}</Typography>
                  </>
                )}
              </Box>
              
              <DialogContentText color="warning.main" sx={{ fontSize: '0.9rem' }}>
                * 3
              </DialogContentText>
            </>
          )}
        </DialogContent>
        
        <DialogActions sx={{ px: 3, pb: 3 }}>
          {dialogStep === 1 ? (
            <>
              <Button onClick={handleCloseDialog} color="inherit">
                
              </Button>
              <Button 
                onClick={handleNextStep} 
                variant="contained" 
                color="primary"
                disabled={numParticipants < 1}
              >
                
              </Button>
            </>
          ) : submitting ? (
            <Box sx={{ width: '100%', display: 'flex', justifyContent: 'center', py: 1 }}>
              <CircularProgress size={30} />
            </Box>
          ) : success ? (
            <Box sx={{ width: '100%', textAlign: 'center', py: 1 }}>
              <Typography color="success.main" sx={{ fontWeight: 500 }}>
                
              </Typography>
            </Box>
          ) : (
            <>
              <Button onClick={handlePrevStep} color="inherit">
                
              </Button>
              <Button 
                onClick={handleSubmit} 
                variant="contained" 
                color="primary"
              >
                
              </Button>
            </>
          )}
        </DialogActions>
      </Dialog>
      
      {/*  */}
      <Snackbar
        open={showAlert}
        autoHideDuration={4000}
        onClose={() => setShowAlert(false)}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert 
          onClose={() => setShowAlert(false)} 
          severity={alertSeverity}
          variant="filled"
        >
          {alertMessage}
        </Alert>
      </Snackbar>
    </Container>
  );
};

export default EventDetailPage; 