import React, { createContext, useState, useEffect, useContext, ReactNode } from 'react';

// 
interface User {
  email: string;
  name?: string;
  profileImage?: string;
}

// 
interface AuthContextType {
  isAuthenticated: boolean;
  user: User | null;
  login: (userData: { email: string }) => void;
  logout: () => void;
}

// 
interface AuthProviderProps {
  children: ReactNode;
}

// 
const defaultAuthContext: AuthContextType = {
  isAuthenticated: false,
  user: null,
  login: () => {},
  logout: () => {}
};

// 
export const AuthContext = createContext(defaultAuthContext as AuthContextType);

// 
export const AuthProvider = ({ children }: AuthProviderProps): JSX.Element => {
  // 
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [user, setUser] = useState(null as User | null);

  // 
  useEffect(() => {
    const storedUser = localStorage.getItem('user');
    if (storedUser) {
      try {
        const parsedUser = JSON.parse(storedUser);
        setUser(parsedUser);
        setIsAuthenticated(true);
      } catch (e) {
        console.error('Failed to parse stored user data:', e);
        localStorage.removeItem('user');
      }
    }
  }, []);

  // 
  const login = (userData: { email: string }) => {
    // URL
    const randomProfileImage = `https://randomuser.me/api/portraits/${Math.random() > 0.5 ? 'men' : 'women'}/${Math.floor(Math.random() * 100)}.jpg`;
    
    // @
    const name = userData.email.split('@')[0];
    
    // 
    const user: User = {
      email: userData.email,
      name,
      profileImage: randomProfileImage
    };
    
    // 
    setUser(user);
    setIsAuthenticated(true);
    localStorage.setItem('user', JSON.stringify(user));
  };

  // 
  const logout = () => {
    setUser(null);
    setIsAuthenticated(false);
    localStorage.removeItem('user');
  };

  return (
    <AuthContext.Provider
      value={{
        isAuthenticated,
        user,
        login,
        logout
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};

// 
export const useAuth = (): AuthContextType => {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};

export default AuthProvider; 