import React, { useState, useContext } from 'react';
import {
  Box,
  Container,
  Typography,
  Grid,
  Card,
  CardContent,
  CardMedia,
  CardActions,
  Button,
  Chip,
  Avatar,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  TextField,
  Snackbar,
  Alert,
  CircularProgress,
  useTheme,
  Divider,
  InputAdornment,
  Tabs,
  Tab,
  IconButton,
  useMediaQuery
} from '@mui/material';
import {
  Search as SearchIcon,
  DateRange as DateRangeIcon,
  LocationOn as LocationOnIcon,
  Group as GroupIcon,
  FilterList as FilterListIcon,
  Favorite as FavoriteIcon,
  FavoriteBorder as FavoriteBorderIcon,
  Share as ShareIcon
} from '@mui/icons-material';
import { motion } from 'framer-motion';
import { AuthContext } from '../context/AuthContext';

// 
const fadeIn = {
  hidden: { opacity: 0, y: 20 },
  visible: { opacity: 1, y: 0 }
};

// 
const categories = [
  '',
  '',
  '',
  '',
  '',
  '',
  ''
];

// 
const eventsData = [
  {
    id: 1,
    title: '',
    description: '',
    image: 'https://images.unsplash.com/photo-1516450360452-9312f5e86fc7?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1950&q=80',
    date: '20231015()',
    time: '19:00 - 22:00',
    location: '',
    participants: 45,
    maxParticipants: 60,
    price: '5,000',
    category: '',
    tags: ['', '', '2030'],
    isFavorite: false
  },
  {
    id: 2,
    title: '',
    description: '',
    image: 'https://images.unsplash.com/photo-1519331379826-f10be5486c6f?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1950&q=80',
    date: '20231022()',
    time: '10:00 - 16:00',
    location: '',
    participants: 18,
    maxParticipants: 25,
    price: '3,500',
    category: '',
    tags: ['', '', ''],
    isFavorite: true
  },
  {
    id: 3,
    title: '',
    description: '',
    image: 'https://images.unsplash.com/photo-1536935338788-846bb9981813?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1986&q=80',
    date: '20231028()',
    time: '18:00 - 21:00',
    location: 'JR',
    participants: 12,
    maxParticipants: 15,
    price: '6,000',
    category: '',
    tags: ['', '', ''],
    isFavorite: false
  },
  {
    id: 4,
    title: '',
    description: '',
    image: 'https://images.unsplash.com/photo-1518998053901-5348d3961a04?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1974&q=80',
    date: '2023115()',
    time: '13:00 - 17:00',
    location: '',
    participants: 8,
    maxParticipants: 20,
    price: '4,500',
    category: '',
    tags: ['', '', ''],
    isFavorite: false
  },
  {
    id: 5,
    title: '',
    description: '',
    image: 'https://images.unsplash.com/photo-1556910103-1c02745adc8b?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1950&q=80',
    date: '20231112()',
    time: '17:00 - 19:00',
    location: 'Zoom',
    participants: 15,
    maxParticipants: 30,
    price: '2,500',
    category: '',
    tags: ['', '', ''],
    isFavorite: true
  },
  {
    id: 6,
    title: '',
    description: '',
    image: 'https://images.unsplash.com/photo-1575052814086-f385e2e2ad1b?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1970&q=80',
    date: '20231118()',
    time: '10:00 - 13:00',
    location: '',
    participants: 25,
    maxParticipants: 30,
    price: '3,000',
    category: '',
    tags: ['', '', ''],
    isFavorite: false
  }
];

const EventsPage: React.FC = () => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const { isAuthenticated } = useContext(AuthContext);
  
  // 
  const [events, setEvents] = useState(eventsData);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('');
  const [selectedEvent, setSelectedEvent] = useState<typeof eventsData[0] | null>(null);
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [successMessage, setSuccessMessage] = useState('');
  const [showSuccess, setShowSuccess] = useState(false);
  const [participantCount, setParticipantCount] = useState(1);
  const [notes, setNotes] = useState('');

  // 
  const filteredEvents = events.filter(event => {
    // 
    if (selectedCategory !== '' && event.category !== selectedCategory) {
      return false;
    }
    
    // 
    if (searchTerm && !event.title.toLowerCase().includes(searchTerm.toLowerCase()) && 
        !event.description.toLowerCase().includes(searchTerm.toLowerCase())) {
      return false;
    }
    
    return true;
  });

  // 
  const handleCategoryChange = (event: React.SyntheticEvent, newValue: string) => {
    setSelectedCategory(newValue);
  };

  // 
  const handleToggleFavorite = (eventId: number) => {
    setEvents(prevEvents => 
      prevEvents.map(event => 
        event.id === eventId 
          ? { ...event, isFavorite: !event.isFavorite } 
          : event
      )
    );
  };

  // 
  const handleOpenDialog = (event: typeof eventsData[0]) => {
    if (!isAuthenticated) {
      // 
      alert('');
      return;
    }
    setSelectedEvent(event);
    setIsDialogOpen(true);
    setParticipantCount(1);
    setNotes('');
  };

  // 
  const handleCloseDialog = () => {
    setIsDialogOpen(false);
    setSelectedEvent(null);
  };

  // 
  const handleSubmitApplication = () => {
    if (!selectedEvent) return;
    
    setIsSubmitting(true);
    
    // 
    setTimeout(() => {
      setSuccessMessage(`${selectedEvent.title}`);
      setShowSuccess(true);
      setIsSubmitting(false);
      handleCloseDialog();
      
      // 
      setEvents(prevEvents => 
        prevEvents.map(event => 
          event.id === selectedEvent.id 
            ? { ...event, participants: event.participants + participantCount } 
            : event
        )
      );
    }, 1500);
  };

  // 
  const handleCloseSuccess = () => {
    setShowSuccess(false);
  };

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Box sx={{ mb: 4 }}>
        <Typography 
          variant="h4" 
          component="h1" 
          gutterBottom
          sx={{ fontWeight: 700 }}
        >
          
        </Typography>
        <Typography variant="body1" color="text.secondary">
          
        </Typography>
      </Box>
      
      {/*  */}
      <Box sx={{ mb: 4 }}>
        <TextField
          fullWidth
          placeholder="..."
          variant="outlined"
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <SearchIcon color="action" />
              </InputAdornment>
            ),
            endAdornment: (
              <InputAdornment position="end">
                <IconButton edge="end">
                  <FilterListIcon />
                </IconButton>
              </InputAdornment>
            ),
          }}
          sx={{ 
            bgcolor: 'background.paper',
            borderRadius: 1,
            boxShadow: theme.shadows[1]
          }}
        />
      </Box>
      
      {/*  */}
      <Box sx={{ mb: 4, borderBottom: 1, borderColor: 'divider' }}>
        <Tabs 
          value={selectedCategory} 
          onChange={handleCategoryChange}
          variant={isMobile ? "scrollable" : "standard"}
          scrollButtons="auto"
          allowScrollButtonsMobile
          sx={{ mb: 0 }}
        >
          {categories.map((category) => (
            <Tab 
              key={category} 
              label={category} 
              value={category}
              sx={{ 
                fontWeight: 600,
                '&.Mui-selected': {
                  color: theme.palette.primary.main,
                }
              }}
            />
          ))}
        </Tabs>
      </Box>
      
      {/*  */}
      {filteredEvents.length > 0 ? (
        <Grid container spacing={3}>
          {filteredEvents.map((event, index) => (
            <Grid item xs={12} sm={6} md={4} key={event.id}>
              <motion.div
                initial="hidden"
                whileInView="visible"
                viewport={{ once: true }}
                variants={fadeIn}
                transition={{ duration: 0.3, delay: index * 0.1 }}
              >
                <Card 
                  sx={{ 
                    height: '100%',
                    display: 'flex',
                    flexDirection: 'column',
                    borderRadius: 2,
                    overflow: 'hidden',
                    boxShadow: theme.shadows[2],
                    transition: 'transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out',
                    '&:hover': {
                      transform: 'translateY(-5px)',
                      boxShadow: theme.shadows[8],
                    }
                  }}
                >
                  <Box sx={{ position: 'relative' }}>
                    <CardMedia
                      component="img"
                      height="180"
                      image={event.image}
                      alt={event.title}
                    />
                    <Box
                      sx={{
                        position: 'absolute',
                        top: 8,
                        right: 8,
                        bgcolor: 'rgba(255, 255, 255, 0.9)',
                        borderRadius: '50%',
                        p: 0.5,
                        zIndex: 1,
                      }}
                    >
                      <IconButton
                        size="small"
                        onClick={(e) => {
                          e.stopPropagation();
                          handleToggleFavorite(event.id);
                        }}
                        color="primary"
                      >
                        {event.isFavorite ? <FavoriteIcon /> : <FavoriteBorderIcon />}
                      </IconButton>
                    </Box>
                    <Chip
                      label={event.category}
                      color="primary"
                      size="small"
                      sx={{
                        position: 'absolute',
                        bottom: 8,
                        left: 8,
                        fontWeight: 600,
                      }}
                    />
                  </Box>
                  <CardContent sx={{ flexGrow: 1, pb: 1 }}>
                    <Typography 
                      gutterBottom 
                      variant="h6" 
                      component="h2"
                      sx={{ fontWeight: 600, mb: 1, lineHeight: 1.3 }}
                    >
                      {event.title}
                    </Typography>
                    <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
                      <DateRangeIcon fontSize="small" color="action" sx={{ mr: 1 }} />
                      <Typography variant="body2" color="text.secondary">
                        {event.date} {event.time}
                      </Typography>
                    </Box>
                    <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
                      <LocationOnIcon fontSize="small" color="action" sx={{ mr: 1 }} />
                      <Typography variant="body2" color="text.secondary">
                        {event.location}
                      </Typography>
                    </Box>
                    <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                      <GroupIcon fontSize="small" color="action" sx={{ mr: 1 }} />
                      <Typography variant="body2" color="text.secondary">
                        {event.participants}/{event.maxParticipants}
                      </Typography>
                      <Box 
                        sx={{ 
                          ml: 1, 
                          flexGrow: 1, 
                          height: 6, 
                          bgcolor: 'rgba(0,0,0,0.1)',
                          borderRadius: 5,
                          overflow: 'hidden'
                        }}
                      >
                        <Box 
                          sx={{ 
                            height: '100%', 
                            width: `${(event.participants / event.maxParticipants) * 100}%`,
                            bgcolor: event.participants >= event.maxParticipants 
                              ? 'error.main' 
                              : event.participants / event.maxParticipants > 0.7 
                                ? 'warning.main' 
                                : 'success.main',
                            borderRadius: 5
                          }} 
                        />
                      </Box>
                    </Box>
                    <Typography 
                      variant="body2" 
                      color="text.secondary"
                      sx={{
                        overflow: 'hidden',
                        textOverflow: 'ellipsis',
                        display: '-webkit-box',
                        WebkitLineClamp: 3,
                        WebkitBoxOrient: 'vertical',
                        mb: 2
                      }}
                    >
                      {event.description}
                    </Typography>
                    <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5, mb: 1 }}>
                      {event.tags.map((tag) => (
                        <Chip
                          key={tag}
                          label={tag}
                          size="small"
                          variant="outlined"
                          sx={{ fontSize: '0.7rem' }}
                        />
                      ))}
                    </Box>
                  </CardContent>
                  <Divider />
                  <CardActions sx={{ justifyContent: 'space-between', px: 2, py: 1.5 }}>
                    <Typography variant="subtitle1" color="primary" sx={{ fontWeight: 600 }}>
                      {event.price}
                    </Typography>
                    <Button 
                      size="small" 
                      variant="contained"
                      color="primary"
                      onClick={() => handleOpenDialog(event)}
                      disabled={event.participants >= event.maxParticipants}
                    >
                      {event.participants >= event.maxParticipants ? '' : ''}
                    </Button>
                  </CardActions>
                </Card>
              </motion.div>
            </Grid>
          ))}
        </Grid>
      ) : (
        <Box sx={{ textAlign: 'center', py: 8 }}>
          <Typography variant="h6" color="text.secondary">
            
          </Typography>
          <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
            
          </Typography>
        </Box>
      )}
      
      {/*  */}
      <Dialog
        open={isDialogOpen}
        onClose={handleCloseDialog}
        maxWidth="sm"
        fullWidth
      >
        {selectedEvent && (
          <>
            <DialogTitle sx={{ pb: 1 }}>
              <Typography variant="h6" component="div" sx={{ fontWeight: 600 }}>
                
              </Typography>
            </DialogTitle>
            <DialogContent dividers>
              <Box sx={{ mb: 3, display: 'flex', alignItems: 'center' }}>
                <Avatar 
                  src={selectedEvent.image} 
                  variant="rounded"
                  sx={{ width: 60, height: 60, mr: 2 }}
                />
                <Box>
                  <Typography variant="subtitle1" sx={{ fontWeight: 600 }}>
                    {selectedEvent.title}
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    {selectedEvent.date} {selectedEvent.time}
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    {selectedEvent.location}
                  </Typography>
                </Box>
              </Box>
              
              <DialogContentText paragraph>
                
              </DialogContentText>
              
              <Box sx={{ mb: 3 }}>
                <Typography variant="subtitle2" gutterBottom>
                  
                </Typography>
                <TextField
                  fullWidth
                  type="number"
                  variant="outlined"
                  value={participantCount}
                  onChange={(e) => setParticipantCount(Math.max(1, Math.min(5, parseInt(e.target.value) || 1)))}
                  inputProps={{ min: 1, max: 5 }}
                  helperText="5"
                  size="small"
                />
              </Box>
              
              <Box sx={{ mb: 2 }}>
                <Typography variant="subtitle2" gutterBottom>
                  
                </Typography>
                <TextField
                  fullWidth
                  multiline
                  rows={4}
                  variant="outlined"
                  placeholder=""
                  value={notes}
                  onChange={(e) => setNotes(e.target.value)}
                />
              </Box>
              
              <Box sx={{ bgcolor: theme.palette.grey[50], p: 2, borderRadius: 1, mb: 2 }}>
                <Typography variant="subtitle2" gutterBottom sx={{ color: theme.palette.primary.main }}>
                  
                </Typography>
                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                  <Typography variant="body2">
                    {participantCount}
                  </Typography>
                  <Typography variant="body2">
                    {isNaN(parseInt(selectedEvent.price.replace(/[^0-9]/g, ''))) 
                      ? selectedEvent.price 
                      : parseInt(selectedEvent.price.replace(/[^0-9]/g, '')) * participantCount + ''}
                  </Typography>
                </Box>
                <Divider sx={{ my: 1 }} />
                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                  <Typography variant="subtitle2" sx={{ fontWeight: 600 }}>
                    
                  </Typography>
                  <Typography variant="subtitle1" sx={{ fontWeight: 600, color: theme.palette.primary.main }}>
                    {isNaN(parseInt(selectedEvent.price.replace(/[^0-9]/g, ''))) 
                      ? selectedEvent.price 
                      : parseInt(selectedEvent.price.replace(/[^0-9]/g, '')) * participantCount + ''}
                  </Typography>
                </Box>
              </Box>
              
              <DialogContentText variant="body2" color="text.secondary">
                3
              </DialogContentText>
            </DialogContent>
            <DialogActions sx={{ px: 3, py: 2 }}>
              <Button 
                onClick={handleCloseDialog} 
                color="inherit"
                disabled={isSubmitting}
              >
                
              </Button>
              <Button 
                onClick={handleSubmitApplication} 
                variant="contained"
                color="primary"
                disabled={isSubmitting}
                startIcon={isSubmitting && <CircularProgress size={20} color="inherit" />}
              >
                
              </Button>
            </DialogActions>
          </>
        )}
      </Dialog>
      
      {/*  */}
      <Snackbar 
        open={showSuccess} 
        autoHideDuration={6000} 
        onClose={handleCloseSuccess}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert 
          onClose={handleCloseSuccess} 
          severity="success" 
          variant="filled"
          sx={{ width: '100%' }}
        >
          {successMessage}
        </Alert>
      </Snackbar>
    </Container>
  );
};

export default EventsPage; 